@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  display: flex;
  width: 90%;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  color: white;
  height: 120px;
  font-size: 36px;
}

@media only screen and (max-width: 950px) {
  .header {
    font-size: 26px;
  }
}

@media only screen and (max-width: 600px) {
  .header {
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
  }
}
.searchbar {
  font-family: Lato;
}

.search-field {
  background-color: #343A40;
  color: #C4C4C4;
  border: none;
  height: 60px;
  width: 52vw;
  padding-left: 15px;
  font-size: 36px;
  font-family: Lato;
  -webkit-appearance: none;
  border-radius: 0;
  box-sizing: border-box;
}

.add-button {
  background-color: #67DBF9;
  color: #343A40;
  border: none;
  height: 60px;
  font-size: 36px;
  font-family: Lato;
  -webkit-appearance: none;
  border-radius: 0;
  padding: 0 10px;
}

label {
  margin-bottom: 0 !important;
}

.dropdown {
  position: absolute;
  width: 52vw;
  z-index: 1;
  max-height: 450px;
  overflow-y: scroll;
}

.dropdown-item {
  min-height: 60px;
  border-top: #454d55 2px solid;
  background-color: #343A40;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px 0 10px 15px;
  box-sizing: border-box;
}

@media only screen and (max-width: 950px) {
  .search-field {
    height: 40px;
    font-size: 20px;
  }
  .add-button {
    height: 40px;
    font-size: 20px;
  }
  
  .dropdown-item {
    min-height: 40px;
    font-size: 20px;
  }
}

@media only screen and (max-width: 600px) {
  .search-field {
    width: 60vw;
  }

  .dropdown {
    width: 60vw;
    max-height: 400px;
  }
}
.player-stat-block {
  width: 28vw;
  background-color: #343A40;
  color: white;
  border: white 1px solid;
  margin-bottom: 25px;
}

.player-stat-head {
  min-height: 36px;
  font-size: 24px;
  border: white 1px solid;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: left;
}

.player-stat-row {
  display: flex;
  height: 67px;
}

.player-stat-row > div {
  border: white 1px solid;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.stat-name {
  font-size: 14px;
}

.stat-num {
  font-size: 20px;
}

.w-1\/3 {
  width: 33.33%;
}

.w-23 {
  width: 23.33%;
}

.w-3\/10 {
  width: 30%;
}

.w-27 {
  width: 27%;
}

.w-19 {
  width: 19%;
}

@media only screen and (max-width: 950px) {
  .player-stat-block {
    width: 85vw;
  }
}

.player-stats-block {
  width: 32%;
  overflow-y: scroll;
  height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media only screen and (max-width: 950px) {
  .player-stats-block {
    width: 100%;
    height: 50vh;
  }
}
.dropdown-menu-container {
  background-color: #343A40;
  color: #ffffff;
  border: none;
  border: 0;
  font-size: 20px;
  height: 50px;
  width: 100px;
  /* -webkit-appearance: none; */
  margin-bottom: 15px;
}

select {
  border-radius: 0;
  border: 0;
  outline: 1px solid #343A40;
  outline-offset: -1px;
}

.dropdown-text-before {
  font-size: 20px;
  color: #ffffff;
  margin-right: 15px;
}


.graph-container {
  width: 65%;
  height: 70vh;
}

.page-empty-text {
  position: absolute;
  font-size: 24px;
  font-family: Lato;
  color: #ffffff;
  top: 30%;
  width: 100%;
  text-align: center;
}

@media only screen and (max-width: 950px) {
  .graph-container {
    margin: 30px 0;
    width: 100%;
    height: 100%;
  }
}
.home {
  text-align: center;
}

.blocks-container {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: 0 auto;
  position: relative;
}

@media only screen and (max-width: 950px) {
  .blocks-container {
    flex-direction: column;
  }
}
table {
  color: #ffffff;
  background-color: #343A40;
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
  position: relative;
}

tr {
  height: 50px;
  font-size: 24px;
}

th {
  border: 1px solid #454d55;
}

td {
  border: 1px solid #454d55;
}
.profile-container {
  background-color: #282C34;
  padding: 45px 140px;
}

.player-name {
  color: #ffffff;
  font-size: 36px;
  text-align: left;
  font-weight: bold;
}

.table-title {
  color: #ffffff;
  font-size: 30px;
  text-align: left;
  margin-top: 25px;
  margin-bottom: 5px;
}

.season-average-table-container {
  overflow: scroll;
}

.previous-game-table-container {
  height: 45vh;
  overflow: scroll;
}

.previous-game-table-header th {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: #343A40;
  border: 1px solid #454d55;
}

.close-button {
  position: absolute;
  right: -95px;
  top: 0;
  cursor: pointer;
}

.home-button {
  color: #ffffff;
  text-decoration: none;
  font-size: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 120px;
  margin-bottom: 10px;
}

@media only screen and (max-width: 950px) {
  .profile-container {
    padding: 10px;
  }
  .close-button {
    right: 5px;
    top: 5px;
  }
  tr {
    font-size: 16px;
    height: 40px;
  }
  th {
    padding: 0px 8px;
  }
  td {
    padding: 0px 8px;
  }
  .player-name {
    font-size: 26px;
  }
  
  .table-title {
    font-size: 22px;
  }
}

@media only screen and (max-width: 600px) {

}
.modal-container {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.15);
}

.modal {
  position: fixed;
  top: 25px;
  left: 10%;
  right: 10%;
  border: 2px solid #444;
}

@media only screen and (max-width: 950px) {
  .modal {
    top: 15vw;
    left: 3%;
    right: 3%;
  }
}

@media only screen and (max-width: 600px) {

}
.App {
  text-align: center;
  font-family: Lato;
  background-color: #282C34;
  min-height: 100vh;
}

